<template>
  <AppMenu :menus="menu" :licenseKey="99"></AppMenu>
</template>

<script>

export default {
  name: 'AppMenuEg',
  data () {
    return {
      menu: [
        {
          label: '',
          path: '/1',
          permission: [1, 2, 3, 99] // licenseKeypermission
        },
        {
          label: '',
          path: '/2',
          children: [
            {
              label: '',
              path: 'sub/1'
            },
            {
              label: '',
              path: 'sub/2'
            },
            {
              label: '',
              path: 'sub/3'
            }
          ]
        },
        { label: '', path: '/3', hidden: true },
        { label: '', path: '/4', permission: [100] }
      ]
    };
  },
  props: [],
  methods: {},
  computed: {
  }
};
</script>

<style lang="scss" scoped>

</style>



